import React, { useState, useEffect } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { Toaster, toast } from "sonner";
import AppRoutes from "./routes/AppRoutes";
import Loader from "./components/Loader";
import { socket } from "./socket";

function App() {
  const [appInitializing, setAppInitializing] = useState(() => {
    return !sessionStorage.getItem("appInitialized");
  });

  // 1. SPLASH SCREEN LOGIC
  useEffect(() => {
    if (!appInitializing) return;

    const timer = setTimeout(() => {
      sessionStorage.setItem("appInitialized", "true");
      setAppInitializing(false);
    }, 2000);

    return () => clearTimeout(timer);
  }, [appInitializing]);

  // 2. SOCKET, ROOM JOINING & GPS WAKE-UP
  useEffect(() => {
    // A. Connect Socket
    socket.connect();

    // B. JOIN THE DRIVER'S PERSONAL ROOM
    // This is critical! Without this, the server doesn't know this is "Driver X"
    const myRegNo = localStorage.getItem("driverRegNo"); // Or your specific key
    if (myRegNo) {
      // console.log("Joining session for:", myRegNo);
      socket.emit("join_app_session", myRegNo);
    }

    // C. Global Listener: Trip Assigned
    // Only triggers if the server sends a message to this driver's room
    const handleDriverAssigned = (data) => {
      // Security Check: Ensure the alert is meant for this driver
      // (Optional if server rooms are set up correctly, but good for safety)
      if (data.driverRegNo && data.driverRegNo !== myRegNo) return;

      toast.info(`New Trip Assigned! #${data.tripId || ""}`, {
        duration: 8000, // Show for 8 seconds
        action: {
          label: "OPEN",
          onClick: () => (window.location.href = "/my-trips"), // Redirects to trips page
        },
      });
    };

    socket.on("driver_assigned", handleDriverAssigned);

    // D. SILENT GPS WAKE-UP
    let watchId;
    if ("geolocation" in navigator) {
      watchId = navigator.geolocation.watchPosition(
        (pos) =>
          console.log("System: GPS Active", pos.coords.accuracy),
        (err) => console.warn("System: GPS Warming up...", err.message),
        {
          enableHighAccuracy: false,
          timeout: 15000,
          maximumAge: 0,
        }
      );
    }

    // Cleanup on App Close
    return () => {
      socket.disconnect();
      socket.off("driver_assigned", handleDriverAssigned);
      if (watchId) navigator.geolocation.clearWatch(watchId);
    };
  }, []);

  // 3. RENDER LOADER
  if (appInitializing) {
    return <Loader message="CONNECTING PATRA ELITE DRIVERS..." />;
  }

  // 4. MAIN APP RENDER
  return (
    <Router>
      <Toaster
        position="top-center"
        richColors
        closeButton
        theme="light"
        toastOptions={{
          style: {
            fontSize: "14px",
            fontWeight: "600",
            fontFamily: "'Inter', sans-serif",
            padding: "16px 24px",
          },
          duration: 3000,
        }}
      />

      <AppRoutes />
    </Router>
  );
}

export default App;
